#hero {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 0;
  min-height: 100vh;
  video {
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    // min-width: 100vw;
    // min-height: 400px;
  }
}

#coverText {
  font-size: 5rem;
  font-family: "Bebas Neue", cursive;
  letter-spacing: 0.5rem;
  position: absolute;
  max-width: 70vw;
  left: 20vw;
  bottom: 10vh;
}

.coverText {
  font-size: min(15vh, 11vw);
  font-family: "Bebas Neue", cursive;
  letter-spacing: 0.5rem;
  position: absolute;
  max-width: 70vw;
  left: 20vw;
  bottom: 10vh;
}
